import React from "react";
import { Button, Col, Figure, Image, Row } from "react-bootstrap";
import {
  ICMinus,
  ICPlus,
  ImgDesigner5,
  ImgSingle1,
  ImgSingleThumb1,
  ImgSingleThumb2,
  ImgSingleThumb3,
  ImgSingleThumb4,
} from "../../assets";
import { BreadCumb, MenuBar } from "../../components";
import "./SingleProduct.scss";

const SingleProduct = () => {
  return (
    <>
      <MenuBar />
      <BreadCumb />
      <div className="container single-product-wrapper">
        <Row>
          <Col lg={5}>
            <Figure>
              <Figure.Image src={ImgSingle1} />
              <Figure.Caption>
                <div className="single-thumb-wrapper bg-white d-flex justify-content-between align-items-center">
                  <a href="#image">
                    <Image src={ImgSingleThumb1} />
                  </a>
                  <a href="#image">
                    <Image src={ImgSingleThumb2} />
                  </a>
                  <a href="#image">
                    <Image src={ImgSingleThumb3} />
                  </a>
                  <a href="#image">
                    <Image src={ImgSingleThumb4} />
                  </a>
                </div>
              </Figure.Caption>
            </Figure>
          </Col>
          <Col>
            <h4>Jeans: Giordano XI</h4>
            <h5>IDR 290.000.000</h5>
            <div className="d-flex align-items-center counter mt-4">
              <Image src={ICMinus} />
              <p className="m-auto">10</p>
              <Image src={ICPlus} />
            </div>
            <div style={{ width: 240, marginTop: 40 }}>
              <Button className="btn-add-cart" block>
                Add to Cart
              </Button>
              <Button variant="secondary" className="btn-wishlist" block>
                Add to Wishlist
              </Button>
            </div>
            <hr />
            <div className="designer-wrapper">
              <h6>Designed by</h6>
              <Row>
                <Col className="d-flex align-items-center">
                  <Image src={ImgDesigner5} />
                  <div className="ml-2">
                    <p className="my-auto name">Anne Mortgery</p>
                    <p className="my-auto followers">14.2K Followers</p>
                  </div>
                </Col>
                <Col></Col>
              </Row>
            </div>
          </Col>
        </Row>
      </div>
    </>
  );
};

export default SingleProduct;
