import React from "react";
import { BrowserRouter, Route, Switch } from "react-router-dom";
import Home from "../Home";
import SingleProduct from "../SingleProduct";
import "./MainApp.scss";

const MainApp = () => {
  return (
    <BrowserRouter>
      <Switch>
        <Route path="/single-product">
          <SingleProduct />
        </Route>
        <Route path="/">
          <Home />
        </Route>
      </Switch>
    </BrowserRouter>
  );
};

export default MainApp;
