import React from "react";
import { Col, Row } from "react-bootstrap";
import "./HeaderSection.scss";

const HeaderSection = ({ title, subtitle }) => {
  return (
    <Row className="header-section">
      <Col>
        <h3>{title}</h3>
        <p>{subtitle}</p>
      </Col>
    </Row>
  );
};

export default HeaderSection;
