import React from "react";
import { Image, Nav, Navbar } from "react-bootstrap";
import { ICCart, ICLogo } from "../../../assets";
import "./MenuBar.scss";

const MenuBar = () => {
  return (
    <div className="navbar-wrapper">
      <Navbar expand="lg" fixed="top" className="navbar py-3">
        <div className="container">
          <a
            href="/"
            atl="logo"
            className="mr-1 d-flex justify-content-center align-items-center"
          >
            <Image src={ICLogo} className="mr-2" />
            <p className="m-auto">My Store</p>
          </a>
          {/* <Navbar.Brand href="#home"> My Store </Navbar.Brand> */}
          <Navbar.Toggle aria-controls="basic-navbar-nav" />
          <Navbar.Collapse id="basic-navbar-nav">
            <Nav className="mx-auto nav-link">
              <Nav.Link href="/home" className="link">
                Home
              </Nav.Link>
              <Nav.Link href="/single-product" className="link">
                Category
              </Nav.Link>
              <Nav.Link href="/designer" className="link">
                Designer
              </Nav.Link>
              <Nav.Link href="/about" className="link">
                About
              </Nav.Link>
            </Nav>
          </Navbar.Collapse>
          <a href="#cart" alt="cart" className="nav-cart">
            <div className="d-flex align-items-center">
              <Image src={ICCart} alt="cart" />
              <span style={{ marginLeft: 5 }}>
                My Cart (<span style={{ fontWeight: 500 }}>10</span>)
              </span>
            </div>
          </a>
        </div>
      </Navbar>
    </div>
  );
};

export default MenuBar;
