import React from "react";
import { Figure } from "react-bootstrap";
import "./FigureCircle.scss";

const FigureCircle = ({ title, subtitle, image }) => {
  return (
    <div className="figure-wrapper text-center">
      <Figure className="d-flex flex-column align-items-center">
        <div className="p-1 rounded-circle img-wrapper mb-2">
          <Figure.Image src={image} fluid roundedCircle />
        </div>
        <Figure.Caption>
          <h6 className="m-0">{title}</h6>
          <p>{subtitle}</p>
        </Figure.Caption>
      </Figure>
    </div>
  );
};

export default FigureCircle;
