import React from "react";
import { BrowserRouter, Route, Switch } from "react-router-dom";
import MainApp from "../../pages/MainApp";

const Routes = () => {
  return (
    <BrowserRouter>
      <Switch>
        <Route path="/">
          <MainApp />
        </Route>
      </Switch>
    </BrowserRouter>
  );
};

export default Routes;
