import React from "react";
import { Figure, Image } from "react-bootstrap";
import { ICCartPlus } from "../../../assets/icon";
import "./FigureSquare.scss";

const FigureSquare = ({ title, subtitle, image }) => {
  return (
    <div className="figure-square-wrapper text-center">
      <Figure>
        <div className="figure-image">
          <Figure.Image src={image} />
          <a
            href="#link"
            className="d-flex justify-content-center align-items-center"
          >
            <Image src={ICCartPlus} alt="cart-plus" className="m-auto" />
          </a>
        </div>
        <Figure.Caption>
          <h6>{title}</h6>
          <p>{subtitle}</p>
        </Figure.Caption>
      </Figure>
    </div>
  );
};

export default FigureSquare;
