import React from "react";
import { Col, Row } from "react-bootstrap";
import {
  ImgDesigner1,
  ImgDesigner2,
  ImgDesigner3,
  ImgDesigner4,
} from "../../../assets/image";
import FigureCircle from "../FigureCircle";
import HeaderSection from "../HeaderSection";
import "./Designer.scss";

const Designer = () => {
  return (
    <div className="container designer-wrapper">
      <HeaderSection
        title="Our Designers"
        subtitle="Pakaian terbaik dari designer profesional"
      />
      <Row className="figure-wrapper text-center">
        <Col xs={6} md={3}>
          <FigureCircle
            title="Anne Mortgery"
            subtitle="Artistic Cloth"
            image={ImgDesigner1}
          />
        </Col>
        <Col xs={6} md={3}>
          <FigureCircle
            title="Anne Mortgery"
            subtitle="Artistic Cloth"
            image={ImgDesigner2}
          />
        </Col>
        <Col xs={6} md={3}>
          <FigureCircle
            title="Anne Mortgery"
            subtitle="Artistic Cloth"
            image={ImgDesigner3}
          />
        </Col>
        <Col xs={6} md={3}>
          <FigureCircle
            title="Anne Mortgery"
            subtitle="Artistic Cloth"
            image={ImgDesigner4}
          />
        </Col>
      </Row>
    </div>
  );
};

export default Designer;
