import React from "react";
import { Col, Image, Nav, Row } from "react-bootstrap";
import { ICLogo } from "../../../assets";
import "./Footer.scss";

const Footer = () => {
  return (
    <div className="container footer-wrapper">
      <Row>
        <Col>
          <Image src={ICLogo} alt="logo" />
        </Col>
        <Col className="text-right">
          <Image src={ICLogo} alt="logo" className="pr-2" />
          <Image src={ICLogo} alt="logo" className="pr-2" />
          <Image src={ICLogo} alt="logo" />
        </Col>
      </Row>
      <Row className="mt-3">
        <Col className="d-flex align-items-center justify-content-start">
          <p className="my-auto">
            All Rights Reserved by My Store Copyright 2021
          </p>
        </Col>
        <Col>
          <Nav
            className="justify-content-end footer-nav"
            activeKey="/home"
            onSelect={(selectedKey) => alert(`selected ${selectedKey}`)}
          >
            <Nav.Item>
              <Nav.Link href="/home">JOBS</Nav.Link>
            </Nav.Item>
            <Nav.Item>
              <Nav.Link eventKey="link-1">DEVELOPER</Nav.Link>
            </Nav.Item>
            <Nav.Item>
              <Nav.Link eventKey="link-2">TERMS</Nav.Link>
            </Nav.Item>
            <Nav.Item>
              <Nav.Link eventKey="link-2">PRIVACY POLICY</Nav.Link>
            </Nav.Item>
          </Nav>
        </Col>
      </Row>
    </div>
  );
};

export default Footer;
