import React from "react";
import { Col, Image, Row } from "react-bootstrap";
import { ImgCc, ImgNike, ImgPnb, imgUniqlo } from "../../../assets";

const Brand = () => {
  return (
    <div className="container py-5">
      <Row className="text-center">
        <Col>
          <Image src={imgUniqlo} alt="uniqlo" className="img-fluid" />
        </Col>
        <Col>
          <Image src={ImgPnb} alt="pnb" className="img-fluid" />
        </Col>
        <Col>
          <Image src={ImgCc} alt="cc" className="img-fluid" />
        </Col>
        <Col>
          <Image src={ImgNike} alt="nike" className="img-fluid" />
        </Col>
      </Row>
    </div>
  );
};

export default Brand;
