import React from "react";
import { Carousel, Col, Image, Row } from "react-bootstrap";
import "./CarouselR.scss";
import { ImgDummy1, ImgDummy2 } from "../../../assets";

const CarouselR = () => {
  return (
    <div className="carousel-wrapper">
      <Carousel className="carousel">
        <Carousel.Item>
          <div className="container">
            <Row className="mt-5">
              <Col xs={12} sm={5} md={{ span: 6, offset: 1 }} lg={5}>
                <h1>Special Eid Lebaran</h1>
                <p>Jadikan hari pertama lebaranmu meriah dan memorable</p>
                <a href="#link" className="btn text-white btn-slide">
                  Get It Now
                </a>
              </Col>
              <Col
                lg={{ span: 5, offset: 1 }}
                sm={5}
                className="d-none d-sm-block"
              >
                <Image src={ImgDummy1} fluid />
              </Col>
            </Row>
          </div>
        </Carousel.Item>
        <Carousel.Item>
          <div className="container">
            <Row className="mt-5">
              <Col xs={12} sm={5} md={{ span: 6, offset: 1 }} lg={5}>
                <h1>Special Ramadhan</h1>
                <p>Jadikan ramadhan mu dengan baju baru</p>
                <a href="#link" className="btn text-white btn-slide">
                  Get It Now
                </a>
              </Col>
              <Col
                lg={{ span: 5, offset: 1 }}
                sm={5}
                className="d-none d-sm-block"
              >
                <Image src={ImgDummy2} fluid />
              </Col>
            </Row>
          </div>
        </Carousel.Item>
      </Carousel>
    </div>
  );
};

export default CarouselR;
